<template>
  <div class="home">
    <nav-bar class="home-nav">
      <div slot="center">购物街</div>
    </nav-bar>
    <tab-control
      :titles="['流行', '新款', '精选']"
      @tabClick="tabClick"
      class="tabFixed"
      ref="tabNav"
      v-show="isTabFixVisible"
    />
    <scroll-view
      ref="scroll"
      class="scroll-wrapper"
      :data="data"
      :listen-scroll="listenScroll"
      :probe-type="probeType"
      :pulldown="pulldown"
      :pullup="pullup"
      @pulldown="pullDown"
      @pullUp="pullUp"
      @scroll="scroll"
    >
      <home-swiper
        :banners="banners"
        class="swiper"
        @swiperImgLoad="swiperImgLoad"
      />
      <recommend-view :recommends="recommends"></recommend-view>
      <fashion></fashion>
      <tab-control
        :titles="['流行', '新款', '精选']"
        @tabClick="tabClick"
        ref="tabControl"
      />
      <goods-list :goodsList="showType"></goods-list>
    </scroll-view>
    <back-top @click.native="backTop" v-show="isShow"></back-top>
  </div>
</template>

<script>
import { getHomeMultiData, getHomeGoods } from "network/home.js";

import NavBar from "components/navbar/NavBar.vue";
import HomeSwiper from "components/swiper/HomeSwiper.vue";
import RecommendView from "./RecommendView.vue";
import Fashion from "./Fashion.vue";
import TabControl from "../../components/tabControl/TabControl.vue";
import GoodsList from "../../components/goodslist/GoodsList.vue";
import ScrollView from "../../components/scroll/ScrollView.vue";
import BackTop from "../../components/backTop/backTop.vue";

export default {
  components: {
    NavBar,
    HomeSwiper,
    RecommendView,
    Fashion,
    TabControl,
    GoodsList,
    ScrollView,
    BackTop,
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      type: "pop",
      data: [],
      pullup: true,
      pulldown: true,
      listenScroll: true,
      probeType: 3,
      isShow: false,
      tabOffsetTop: 0,
      isTabFixVisible: false,
      saveY: 0,
    };
  },
  computed: {
    showType() {
      return this.goods[this.type].list;
    },
  },
  methods: {
    tabClick(index) {
      switch (index) {
        case 0:
          this.type = "pop";
          break;
        case 1:
          this.type = "new";
          break;
        case 2:
          this.type = "sell";
          break;
        default:
          break;
      }
      this.$refs.tabControl.curIdx = this.$refs.tabNav.curIdx = index;
    },
    backTop() {
      this.$refs.scroll.scrollTo(0, 0, 500);
    },
    scroll(pos) {
      this.isShow = pos.y < -1000;
      this.isTabFixVisible = pos.y <= -this.offsetTop;
    },
    pullUp() {
      console.log("上拉加载更多");
      this.getHomeGoodsList(this.type);
    },
    pullDown() {
      console.log("下拉刷新");
    },
    swiperImgLoad() {
      this.offsetTop = this.$refs.tabControl.$el.offsetTop;
    },
    getHomeBannerData() {
      getHomeMultiData()
        .then((resp) => {
          this.banners = resp.data.banner.list;
          this.recommends = resp.data.recommend.list;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    getHomeGoodsList(type) {
      this.type = type;
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then((response) => {
        this.goods[type].list.push(...response.data.list);
        this.goods[type].page += 1;
        this.$refs.scroll.finishPullUp();
      });
    },
  },
  created() {
    this.getHomeBannerData();
    this.getHomeGoodsList("pop");
    this.getHomeGoodsList("new");
    this.getHomeGoodsList("sell");
  },
  mounted() {},
  activated() {
    this.$refs.scroll.scrollTo(0, this.saveY);
    this.$refs.slider && this.$refs.slider.$emit("autoplayStart", 1500);
  },
  deactivated() {
    this.saveY = this.$refs.scroll.getScrollY();
    this.$refs.slider && this.$refs.slider.$emit("autoplayStop");
  },
};
</script>

<style lang="less" scoped>
.home {
  height: 100vh;
  position: relative;
  .home-nav {
    background-color: var(--color-tint);
    color: #fff;
  }

  .scroll-wrapper {
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 49px;
    left: 0;
    right: 0;
  }

  .tabFixed {
    position: relative;
    background-color: #fff;
    z-index: 10;
  }
}
</style>
